body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.app__sidebar {
  padding: 15px;
  background-color: #fff;
  width: 85%;
  max-width: 330px;
}

.app__sidebar-top {
  margin-bottom: 30px;
}
.form__field {
    box-sizing: border-box;
    border: 1px solid #BEBEBE;
    padding: 7px;
    margin: 0;
    width: 100%;
    transition: all 0.3s ease-in-out;
    outline: none;
    font-size: 14px;
}

.form__field--error {
    border-color: #e83232;
}
.list__list {
    padding: 0;
    list-style: none;
}
.list-item {
    margin: 3px 0;
    padding: 0 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    width: 100%;
    text-align: left;
    background-color: #fff;
    border: 1px solid #BEBEBE;
    border-radius: 4px;
}

.list-item__content {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.list-item__delete-button {
    padding: 0;
    border: none;
    outline: none;
    background: none;
    color: #282c34;
    cursor: pointer;
}
.map {
    width: 100%;
    height: 100vh;
}

.map__warning {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #e83232;
}
.toggle-button {
    margin-left: -2px;
    padding: 0;
    width: 50px;
    height: 48px;
    position: absolute;
    top: 16px;
    left: 0;
    color: #282c34;
    border-radius: 2px;
    background: white;
    border: 0;
    box-shadow: 0 0 8px rgba(0,0,0,0.3);
    cursor: pointer;
}
